.header-color {
  background: #00B4DB;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #0083B0, #00B4DB);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #0083B0, #00B4DB); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.landing-grid {
  background: #00B4DB;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #0083B0, #00B4DB);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #0083B0, #00B4DB); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.image-me {
  height: 250px;
  margin-top: 5em;
  width: 250px;
  border-radius: 100px;
}

.banner-text {
  background-color: #000000;
  opacity: 0.5;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  color: #ffffff;
  font-size: 66px;
  font-weight: bold;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: #ffffff;
  font-size: 20px;
  padding: 1em;
}